<template>
  <div class="register__wrap py-7 py-lg-8 pt-lg-9">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-lg-5 col-md-7">
          <div class="card py-5 bg-secondary shadow border-0">
            <div class="h1 text-center">Login to Your accaunt</div>

            <div class="card-body px-lg-5 py-lg-3">
              <ValidationObserver ref="observer" v-slot="{ valid }">
                <form role="form">
                  <ValidationProvider
                    vid="Email"
                    ref="provider"
                    name="Email"
                    rules="required|email"
                    v-slot="{ errors }"
                  >
                    <base-input
                      name="Email"
                      class="input-group-alternative mb-3"
                      placeholder="E-mail"
                      addon-left-icon="ni ni-email-83"
                      v-model="model.email"
                      :error="errors[0]"
                    ></base-input>
                  </ValidationProvider>

                  <ValidationProvider
                    vid="Password"
                    name="Password"
                    rules="required|max:12|min:4"
                    v-slot="{ errors }"
                  >
                    <base-input
                      class="input-group-alternative"
                      placeholder="Your Password"
                      type="password"
                      addon-left-icon="ni ni-lock-circle-open"
                      v-model="model.password"
                      :error="errors[0]"
                    ></base-input>
                  </ValidationProvider>

                  <div class="form-inline justify-content-between">
                    <base-checkbox>Remember me</base-checkbox>

                    <router-link to="/reset" class="text-black text-underline">
                      <small>Forgot password?</small>
                    </router-link>
                  </div>
                  <button
                    :disabled="!valid"
                    @click.prevent="onSave"
                    to="/"
                    class="btn btn-block btn-primary mt-4 btn-block"
                  >Login</button>
                  <router-link
                    to="/"
                    class="btn btn-block btn-primary my-3 btn-block btn-outline-primary"
                  >Continue with google</router-link>

                  <div class="text-center">
                    Don't have an account?
                    <router-link class="text-underline" to="/register">Create one now</router-link>
                  </div>
                </form>
              </ValidationObserver>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "login",

  data() {
    return {
      model: {
        email: "",
        password: ""
      }
    };
  },
  created() {
    console.log(this.$store);
  },
  methods: {
    onSave() {
      const user = this.$store.state.users.find(
        el =>
          this.model.email === el.email && this.model.password === el.password
      );

      if (user) {
        localStorage.setItem("user", JSON.stringify(user));
        this.$router.push("/");
      } else {
        this.$notify({ type: "danger", message: "Неверные данные" });
      }
    }
  }
};
</script>
<style>
.register__wrap {
  background: #e1e1e1b8;
}
</style>
