import Vue from 'vue';
import Router from 'vue-router';
import DashbordLayout from '@/pages/Dashbord/HomeLayout';
import AuthLayout from './pages/Autentification/AutentificationLayout.vue';
import Tasks from '@/pages/Dashbord/Tasks';
import Payment from '@/pages/Dashbord/Payment';
import Users from '@/pages/Dashbord/Users';
import Widthdraw from '@/pages/Dashbord/Widthdraw';

import Login from '@/pages/Autentification/Login';
import Register from '@/pages/Autentification/Register';
import Reset from '@/pages/Autentification/Reset';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: DashbordLayout,
      redirect: 'tasks',
      children: [
        {
          path: 'tasks',
          component: Tasks,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'payment',
          component: Payment,
          meta: {
            requiresAuth: true,
            is_admin: true
          }
        },
        {
          path: 'users',
          component: Users,
          meta: {
            requiresAuth: true,
            is_admin: true
          }
        },
        {
          path: 'widthdraw',
          component: Widthdraw,
          meta: {
            requiresAuth: true,
            is_admin: true
          }
        }
      ]
    },
    {
      path: '/',
      redirect: 'login',
      component: AuthLayout,
      children: [
        {
          path: '/login',
          component: Login,
          meta: {
            guest: true
          }
        },
        {
          path: '/register',
          component: Register,
          meta: {
            guest: true
          }
        },
        {
          path: '/reset',
          component: Reset,
          meta: {
            guest: true
          }
        }
      ]
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('user') == null) {
      next({
        path: '/login'
      });
    } else {
      // let user = JSON.parse(localStorage.getItem('user'));
      // if (to.matched.some(record => record.meta.is_admin)) {
      //   if (user.is_admin == 1) {
      //     next();
      //   } else {
      //     next({ name: 'userboard' });
      //   }
      // } else {
      next();
      // }
    }
  } else if (to.matched.some(record => record.meta.guest)) {
    if (localStorage.getItem('user') == null) {
      next();
    } else {
      next({ path: '/' });
    }
  } else {
    next();
  }
});

export default router;
